.navbar {
	height: 74px;
	background-color: #fff;
}

.navbar .container {
	position: absolute;
    top: 14px;
    left: 0;
    right: 0;
    margin-right: auto;
    margin-left: auto;
}

.navbar-brand {
	font-size: 22px;
}

.navbar-nav > li > a {
	font-size: 16px;
}

.navbar-default .navbar-nav > li > a:hover {
    color: #99FF99;
}

.navbar-default .navbar-nav > li > a.activePage {
    color: #99FF99;
}

@media screen and (max-width:768px) {
	.navbar-default .navbar-collapse, .navbar-default .navbar-form {
		    background: #FFF;
				margin-top: 9px;
	}
}

/* 이벤트 정의 */
@-webkit-keyframes slowView {
    from {opacity:0;}
    to {opacity:1;}
}
@keyframes slowView {
    from {opacity:0;}
    to {opacity:1;}
}

.HideElement {
	opacity: 0;
}

.EventShowElement {
	-webkit-animation: slowView 3s 1;
    animation: slowView 3s 1;
	opacity: 1;
}
/* 이벤트 */
.MainBody {
	width : 100%;
	height : auto;
	position: absolute;

	-webkit-animation: slowView 1s 1;
    animation: slowView 1s 1;
}

.SubContent {
	width: 100%;
	height : 800px;
	padding-top: 74px;
}

.SubContent[name=InquiryPage] {
	padding-top: 148px;
}

.SubContent[name=MainPage] {
	background-image : url(/static/media/image2.e7bae7b4.jpeg);
	background-repeat : no-repeat;
}

.SubContent[name=SkillPage] {
	background-image : url(/static/media/image4.8d9d8b27.jpg);
	background-repeat : no-repeat;
}

.SubContent[name=ActivityPage] {
	background-image : url(/static/media/image1.3e6bb52b.jpeg);
	background-repeat : no-repeat;
}

.parallax-scrolling.parallax-top {
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
}
.parallax-scrolling.parallax-center {
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


/* MainPage */
.MainPage {
	position: relative;
	width : 480px;
	top : 240px;
	right : 0px;
	left : 24px;
	margin-left: auto;
	margin-right: 0px;
}

.MainPage > .MainText {
	color : #333333;
	font-size: 52px;
	font-weight: bold;
}


.MainPage > .MainText > p > code{
	color: #66CC66;
    background-color: transparent;
}

.MainPage > .SubText {
	padding-top: 52px;
	color : #333333;
	font-size: 28px;
	font-weight: bold;
	font-style: oblique;
}
/* MainPage END */


